//
// Fonts
//

// Google Fonts - Inter and Nunito
// These are loaded via link tags in layout.tsx for better performance
// Inter is used as the primary font
// Nunito is used as a secondary font

// Local Inter font files (fallback)
@font-face {
    font-family: 'Inter';
    font-style:  normal;
    font-weight: 300;
    font-display: swap;
    src: url("../../../fonts/inter-light.woff2?v=3.13") format("woff2"),
         url("../../../fonts/inter-light.woff?v=3.13") format("woff");
}

@font-face {
    font-family: 'Inter';
    font-style:  normal;
    font-weight: 400;
    font-display: swap;
    src: url("../../../fonts/inter-regular.woff2?v=3.13") format("woff2"),
         url("../../../fonts/inter-regular.woff?v=3.13") format("woff");
}

@font-face {
    font-family: 'Inter';
    font-style:  normal;
    font-weight: 500;
    font-display: swap;
    src: url("../../../fonts/inter-medium.woff2?v=3.13") format("woff2"),
         url("../../../fonts/inter-medium.woff?v=3.13") format("woff");
}

@font-face {
    font-family: 'Inter';
    font-style:  normal;
    font-weight: 700;
    font-display: swap;
    src: url("../../../fonts/inter-bold.woff2?v=3.13") format("woff2"),
